@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    background-image: url(../img/header.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    height: 80rem;
    position: relative;
}
.header__content {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgb(0 0 0 /75%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.header__title, 
.header__title--names {
    display: block;
    color: v.$blanco;
    line-height: 6rem;
    text-shadow: 0 0 3rem v.$blanco;
}
.header__title--names {
    margin-top: 3rem;
    text-transform: capitalize;
    font-family: v.$fuente_nombres;
    font-size: 5rem;
    font-weight: 300;
    @include m.tablet {
        font-size: 8rem;
    }
}
.header__title {
    margin-top: 4rem;
    font-family: v.$fuente_1;
    font-size: 3.3rem;
    @include m.tablet {
        font-size: 5.3rem;
    }
}
/*countdown*/
.header__countdown {
    display: grid;
    grid-template-columns: repeat(4,1fr);
}
@media (max-width:425px){
    .header__countdown {
        display: grid;
        grid-template-columns: repeat(2,1fr);
        row-gap: 2rem;
    }
}
.header__timer {
    display: grid;
    place-items: center;
    margin-right: 2rem;
}
.header__timer:last-child {
    margin-right: 0;
}
.header__timer-unit {
    font-size: 2rem;
    color: var(--blanco);
    text-transform: capitalize;
    font-family: v.$fuente_texto;
}
.header__timer-hours {
    width: 8rem;
    height:8rem;
    border-radius: 100%;
    border: 1px solid v.$resaltado_4;
    display: grid;
    place-items: center;
    box-shadow: .5rem .5rem 3rem 2rem rgba(0, 0, 0, 0.5);
    font-size: 3rem;
    color: v.$blanco;
    font-family:'Times New Roman', Times, serif;
}
.header__title, 
.header__title--names,
.header__timer-unit,
.header__timer-hours {
    color: v.$resaltado_4;
}
.diamond {
    visibility: hidden;
    &.active {
        visibility: visible;
    }
}