@use 'variables' as v;

.music {
    position: fixed;
    right: 2rem;
    bottom: 2rem;
    width: 5rem;
    height: 5rem;
    border: none;
    border-radius: 50%;
    overflow: hidden;
    z-index: 100;
    visibility: hidden;
    &.active {
        visibility: visible;
    }
    .svg-btn {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    svg {
        display:inline-block;
        width: 50%;
        height: 50%;
    }
}
.reproducir {
    background-color: v.$resaltado;

    .svg-btn {
        background: url(../img/play-fill-2.svg) no-repeat center;
        width: 100%;
        height: 100%;
    }
    &:hover {
        cursor: pointer;
        background-color: v.$gris_claro;
        .svg-btn {
            background: url(../img/play-fill.svg) no-repeat center;
            width: 100%;
            height: 100%;
        }
    }
}
.pausar {
    background-color: v.$gris_claro;

    .svg-btn {
        background: url(../img/pause-fill.svg) no-repeat center;
        width: 100%;
        height: 100%;
    }
    &:hover {
        cursor: pointer;
        background-color: v.$resaltado;
        .svg-btn {
            background: url(../img/pause-fill-2.svg) no-repeat center;
            width: 100%;
            height: 100%;
        }
    }
}