/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-image: url(../img/fondo5.jpg);
  background-repeat: no-repeat;
  color: #f3f3f3;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
}

.container {
  background-color: #656565;
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
  box-shadow: 0px 1px 6px 2px rgba(113, 113, 113, 0.75);
}

section {
  padding: 2rem;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.popup {
  z-index: 1000;
  border: 0.1rem solid #5d492c;
  padding: 2rem;
  padding-top: 5rem;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #f3f3f3;
  box-shadow: 0 2.5rem 5rem rgba(0, 0, 0, 0.1), 0 0 0 100rem rgba(0, 0, 0, 0.7);
  visibility: hidden;
}
.popup p, .popup h2 {
  color: #8f784d;
  text-align: center;
}
.popup .close {
  position: absolute;
  top: 0;
  right: 0;
  padding: 1rem 2rem;
  background-color: #8f784d;
  border: none;
  color: #f3f3f3;
  font-family: "Playfair Display SC", serif;
  font-weight: 600;
}
.popup .close:hover {
  background-color: #656565;
  cursor: pointer;
}
.popup.active {
  visibility: visible;
}

#my-canvas {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  visibility: hidden;
}
#my-canvas.active {
  visibility: visible;
}

h1, h2, h3 {
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  line-height: 1.5;
  font-weight: 700;
  color: #f3f3f3;
  font-size: 2rem;
  font-family: "Noto Serif Thai", serif;
}

.music {
  position: fixed;
  right: 2rem;
  bottom: 2rem;
  width: 5rem;
  height: 5rem;
  border: none;
  border-radius: 50%;
  overflow: hidden;
  z-index: 100;
  visibility: hidden;
}
.music.active {
  visibility: visible;
}
.music .svg-btn {
  display: flex;
  align-items: center;
  justify-content: center;
}
.music svg {
  display: inline-block;
  width: 50%;
  height: 50%;
}

.reproducir {
  background-color: #8f784d;
}
.reproducir .svg-btn {
  background: url(../img/play-fill-2.svg) no-repeat center;
  width: 100%;
  height: 100%;
}
.reproducir:hover {
  cursor: pointer;
  background-color: #cbcbcb;
}
.reproducir:hover .svg-btn {
  background: url(../img/play-fill.svg) no-repeat center;
  width: 100%;
  height: 100%;
}

.pausar {
  background-color: #cbcbcb;
}
.pausar .svg-btn {
  background: url(../img/pause-fill.svg) no-repeat center;
  width: 100%;
  height: 100%;
}
.pausar:hover {
  cursor: pointer;
  background-color: #8f784d;
}
.pausar:hover .svg-btn {
  background: url(../img/pause-fill-2.svg) no-repeat center;
  width: 100%;
  height: 100%;
}

.header {
  background-image: url(../img/header.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 80rem;
  position: relative;
}

.header__content {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.75);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.header__title,
.header__title--names {
  display: block;
  color: #f3f3f3;
  line-height: 6rem;
  text-shadow: 0 0 3rem #f3f3f3;
}

.header__title--names {
  margin-top: 3rem;
  text-transform: capitalize;
  font-family: "MonteCarlo", cursive;
  font-size: 5rem;
  font-weight: 300;
}
@media (min-width: 768px) {
  .header__title--names {
    font-size: 8rem;
  }
}

.header__title {
  margin-top: 4rem;
  font-family: "Playfair Display SC", serif;
  font-size: 3.3rem;
}
@media (min-width: 768px) {
  .header__title {
    font-size: 5.3rem;
  }
}

/*countdown*/
.header__countdown {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

@media (max-width: 425px) {
  .header__countdown {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 2rem;
  }
}
.header__timer {
  display: grid;
  place-items: center;
  margin-right: 2rem;
}

.header__timer:last-child {
  margin-right: 0;
}

.header__timer-unit {
  font-size: 2rem;
  color: var(--blanco);
  text-transform: capitalize;
  font-family: "Noto Serif Thai", serif;
}

.header__timer-hours {
  width: 8rem;
  height: 8rem;
  border-radius: 100%;
  border: 1px solid #b2986d;
  display: grid;
  place-items: center;
  box-shadow: 0.5rem 0.5rem 3rem 2rem rgba(0, 0, 0, 0.5);
  font-size: 3rem;
  color: #f3f3f3;
  font-family: "Times New Roman", Times, serif;
}

.header__title,
.header__title--names,
.header__timer-unit,
.header__timer-hours {
  color: #b2986d;
}

.diamond {
  visibility: hidden;
}
.diamond.active {
  visibility: visible;
}

.invitation {
  background-color: #f3f3f3;
}
.invitation .title {
  color: #1F2020;
  text-align: center;
  font-family: "Playball", cursive;
  font-weight: 300;
  font-size: 4.2rem;
}
@media (min-width: 768px) {
  .invitation .title {
    font-size: 5rem;
  }
}
.invitation .text {
  text-align: justify;
  color: #1F2020;
  text-align: center;
}
.invitation .content {
  text-align: justify;
  color: #1F2020;
  text-align: center;
}
.invitation .spouses {
  width: 80%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .invitation .spouses {
    width: 85%;
  }
  .invitation .spouses .wife {
    text-align: left;
    padding-left: 5rem;
  }
  .invitation .spouses .husband {
    text-align: right;
    padding-right: 5rem;
  }
}
.invitation .spouses p {
  color: #8f784d;
  text-align: center;
  font-size: 4rem;
  line-height: 4rem;
  font-family: "MonteCarlo", cursive;
}
@media (min-width: 385px) {
  .invitation .spouses p {
    line-height: 1.9rem;
  }
}
@media (min-width: 768px) {
  .invitation .spouses p {
    font-size: 4rem;
    line-height: 1.3rem;
  }
}
@media (min-width: 1024px) {
  .invitation .spouses p {
    font-size: 5.5rem;
    line-height: 1.3rem;
  }
}

.godparents-info {
  background-color: #1F2020;
  font-family: "Playfair Display SC", serif;
}
.godparents-info .adorno {
  width: 60%;
  margin: 0 auto;
  font-size: 4.2rem;
}
@media (min-width: 768px) {
  .godparents-info .adorno {
    font-size: 5rem;
  }
}
.godparents-info .border {
  border: 0.4rem solid #8f784d;
  padding: 2rem;
}
.godparents-info .title {
  color: #746a64;
  font-family: "Playball", cursive;
  font-weight: 300;
  font-size: 4.2rem;
}
@media (min-width: 768px) {
  .godparents-info .title {
    font-size: 6rem;
  }
}
.godparents-info .godparent {
  font-size: 2.3rem;
}
.godparents-info .godparents {
  margin: 0 auto;
  text-align: center;
}
.godparents-info .godparents p {
  font-size: 2.5rem;
  font-weight: 300;
  font-family: "Simonetta", cursive;
  color: #d3c0a0;
}
@media (min-width: 1024px) {
  .godparents-info .godparents p {
    font-size: 3rem;
  }
}
@media (min-width: 768px) {
  .godparents-info .left {
    text-align: left;
  }
  .godparents-info .right {
    text-align: right;
  }
  .godparents-info .godparents {
    width: 80%;
    margin: 0 auto;
  }
}

.cards-info {
  background-color: #f3f3f3;
}
.cards-info .title {
  font-family: "Playball", cursive;
  color: #1F2020;
  text-align: center;
  font-weight: 300;
  font-size: 4.2rem;
}
@media (min-width: 768px) {
  .cards-info .title {
    font-size: 5rem;
  }
}
.cards-info .cards {
  display: grid;
  row-gap: 2rem;
}
@media (min-width: 768px) {
  .cards-info .cards {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }
}

.card {
  text-align: center;
  padding-bottom: 3rem;
  z-index: 10;
}
.card .card-title {
  font-size: 4rem;
  color: #f3f3f3;
  font-family: "Simonetta", cursive;
}
.card p {
  text-align: center;
  color: #d3c0a0;
  font-family: "Noto Serif Thai", serif;
  font-weight: 300;
}
.card .maps {
  display: inline-block;
  margin-top: 2rem;
  border: none;
  padding: 1.5rem 2.5rem;
  background-color: #8f784d;
  color: #f3f3f3;
  font-family: "Noto Serif Thai", serif;
  font-weight: 300;
}
.card .maps:hover {
  cursor: pointer;
  background-color: #656565;
  color: #f3f3f3;
}

.ceremonia {
  background-image: url(../img/ceremonia.jpg);
  background-size: cover;
}

.recepcion {
  background-image: url(../img/recepcion.jpg);
  background-size: cover;
}

.footer {
  background-color: #1F2020;
}
.footer p {
  letter-spacing: 1rem;
  text-align: center;
  color: #f3f3f3;
  font-family: "Noto Serif Thai", serif;
  font-weight: 300;
  opacity: 0.5;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .footer p {
    font-size: 3rem;
  }
}
.footer .creator {
  font-weight: bold;
  text-transform: capitalize;
}