@use 'variables' as v;

h1, h2, h3 {
    margin: 0 0 calc(v.$separacion / 2) 0 ;
    font-weight: 900;
    text-align: center;
}
h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
    text-align: center;
}
p {
    line-height: 1.5;
    font-weight: 700;
    color: v.$blanco;
    font-size: 2rem;
    font-family: v.$fuente_texto;
}
