@use 'variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    background-image: url(../img/fondo5.jpg);
    background-repeat: no-repeat;
    color: v.$blanco;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-size: 1.6rem;
}
.container {
    background-color: v.$gris;
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
    box-shadow: 0px 1px 6px 2px rgba(113, 113, 113, 0.75);
}
section {
    padding: 2rem;
}
a {
    text-decoration: none;
}

img {
    max-width: 100%;
}
.popup {
    z-index: 1000;
    border: .1rem solid v.$resaltado_3;
    padding: 2rem;
    padding-top: 5rem;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: v.$blanco;
    box-shadow: 0 2.5rem 5rem rgba(0, 0, 0, 0.1), 0 0 0 100rem rgba(0, 0, 0, 0.7);
    visibility: hidden;
    p, h2 {
        color: v.$resaltado;
        text-align: center;
    }
    .close {
        position: absolute;
        top: 0;
        right: 0;
        padding: 1rem 2rem;
        background-color: v.$resaltado;
        border: none;
        color: v.$blanco;
        font-family: v.$fuente_1;
        font-weight: 600;
        &:hover {
            background-color: v.$gris;
            cursor: pointer;
        }
    }
    &.active {
        visibility: visible;
    }
}
#my-canvas {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    visibility: hidden;
    &.active {
        visibility: visible;
    }
}

