@use 'base/variables' as v;
@use 'base/mixins' as m;


.footer {
    background-color: v.$negro;
    p {
        letter-spacing: 1rem;
        text-align: center;
        color: v.$blanco;
        font-family: v.$fuente_texto;
        font-weight: 300;
        opacity: .5;
        font-size: 2rem;
        @include m.tablet {
            font-size: 3rem;
        }
    }
    .creator {
        font-weight: bold;
        text-transform: capitalize;
    }
}