@use 'base/variables' as v;
@use 'base/mixins' as m;

.godparents-info {
    background-color: v.$negro;
    font-family: v.$fuente_1;
    .adorno {
        width: 60%;
        margin: 0 auto;
        font-size: 4.2rem;
        @include m.tablet {
            font-size: 5rem;
        }
    }
    .border {
        border: .4rem solid v.$resaltado;
        padding: 2rem; 
    }
    .title {
        color: v.$resaltado_5;
        font-family: v.$fuente_titulo;
        font-weight: 300;
        font-size: 4.2rem;
        @include m.tablet {
            font-size: 6rem;
        }
    }
    .godparent {
        font-size: 2.3rem;
    }
    .godparents {
        margin: 0 auto;     
        text-align: center;     
        p {
            font-size: 2.5rem;
            font-weight: 300;
            font-family: v.$fuente_cursiva;
            color: v.$resaltado-2;
            @include m.desktop {
                font-size: 3rem;
            }
            
        }     
    }
    @include m.tablet {
        .left {
            text-align: left;
        }
        .right {
            text-align: right;
        }
        .godparents {
            width: 80%;
            margin: 0 auto;               
        }
    }       
}