@use 'base/variables' as v;
@use 'base/mixins' as m;

.cards-info {
    background-color: v.$blanco;
    .title {
        font-family: v.$fuente_titulo;
        color: v.$negro;
        text-align: center;
        font-weight: 300;
        font-size: 4.2rem;
        @include m.tablet {
            font-size: 5rem;
        }
    }

    .cards {
        display: grid;
        row-gap: 2rem;

        @include m.tablet {
            grid-template-columns: repeat(2,1fr);
            column-gap: 2rem;
        }
    }

}
.card {
    text-align: center;
    padding-bottom: 3rem;
    z-index: 10;

    .card-title {
        font-size: 4rem;
        color: v.$blanco;
        font-family: v.$fuente_cursiva;
    }
    p {
        text-align: center;
        color: v.$resaltado_2;
        font-family: v.$fuente_texto;
        font-weight: 300;
    }
    .maps {
        display: inline-block;
        margin-top: 2rem;
        border: none;
        padding: 1.5rem 2.5rem;
        background-color: v.$resaltado;
        color: v.$blanco;
        font-family: v.$fuente_texto;
        font-weight: 300;
        &:hover {
            cursor: pointer;
            background-color: v.$gris;
            color: v.$blanco;
        }
    }
}
.ceremonia {
    background-image: url(../img/ceremonia.jpg);
    background-size: cover;
}
.recepcion {
    background-image: url(../img/recepcion.jpg);
    background-size: cover;
}