@use 'base/variables' as v;
@use 'base/mixins' as m;

.invitation {
    background-color: v.$blanco;
    .title {
        color: v.$negro;
        text-align: center;
        font-family: v.$fuente_titulo;
        font-weight: 300;
        font-size: 4.2rem;
        @include m.tablet {
            font-size: 5rem;
        }
    }
    .text {
        text-align: justify;
        color: v.$negro;
        text-align: center;
    }
    .content {
        text-align: justify;
        color: v.$negro;
        text-align: center;
    }
    .spouses {
        width: 80%;
        margin: 0 auto;

        @include m.tablet {
            width: 85%;
            .wife {
                text-align: left;
                padding-left: 5rem;
            }
            .husband {
                text-align: right;
                padding-right: 5rem;
            }
        }
        p {
            color: v.$resaltado;
            text-align: center;
            font-size: 4rem;
            line-height: 4rem;
            font-family: v.$fuente_nombres;
            @include m.telefono {
                line-height: 1.9rem;
            }
            @include m.tablet {
                font-size: 4rem;
                line-height: 1.3rem;
            }
            @include m.desktop {
                font-size: 5.5rem;
                line-height: 1.3rem;
            }
        }
    }
}